<template>
    <div id="resumenCompraComponent">
        <h3>Resumen de compra</h3>
            <hr>
            <div class="articulos">
                <p>{{ cantidadArticulos }} artículos</p>
                <p>${{ calcularTotal }}</p>
            </div>
            <form action="">
                <div>
                    <label for="envio">Envío</label>
                    <select name="envio" id="envio" v-model="pagoEnvio">
                        <option value="5000">Envío express $5.000</option>
                        <option value="3000">Envío normal $3.000</option>
                    </select>
                </div>
                <div>
                    <label for="cod">Código de descuento</label>
                    <input type="text" name="cod" id="cod" placeholder="Ingresa tu código" v-model="codigoDescuento">
                    <p id="evaluacion">{{ evaluarCodigo }}</p>
                </div>
                <hr>
                <div>
                    <p>PRECIO TOTAL ${{ calcularCostoFinal }}</p>
                </div>
                <button id="btn" type="button" @click="aprobar">Realizar compra</button>
            </form>
    </div>
</template>

<script>
export default {
    name: 'resumenCompraComponent',
    props: {
        numeroProductos: {
            type: Array,
            required: true
        },
        pagoTotal: {
            type: Array,
            required: true
        }
    },
    data: function() {
        return {
            pagoEnvio: 0,
            codigoDescuento: '',
            descuento: 0,
            subTotal: 0,
            costoFinal: 0,
            contadorClicks: 0
        }
    },
    methods: {
        aprobar: function() {
            this.contadorClicks++;
            if(this.contadorClicks == 1) {
                let btnComprar = document.getElementById('btn');
                btnComprar.style.backgroundColor="yellow";
                btnComprar.style.color="black"
                btnComprar.innerText="¿Estás seguro?"
            }
            else if(this.contadorClicks == 2) {
                let btnComprar = document.getElementById('btn');
                btnComprar.style.backgroundColor="blue";
                btnComprar.style.color="white";
                btnComprar.innerText="¡OK!";
            }
        }
    },
    computed: {
        cantidadArticulos() {
            let total = this.numeroProductos.reduce((acumulador, currentValue) => {
                return acumulador + currentValue;
            }, 0);
            return total;
        },
        calcularTotal() {
            this.subTotal = this.pagoTotal.reduce((acumulador, currentValue) => {
                return acumulador + currentValue;
            }, 0);
            return this.subTotal;
        },
        evaluarCodigo() {
            if(this.codigoDescuento == 'JQUERY2222') {
                //let laCaja = document.getElementById('cod');
                // laCaja.style.backgroundColor="green";
                this.descuento = 20000;
                return 'CÓDIGO VÁLIDO';
            }
        },
        calcularCostoFinal() {
            this.costoFinal = this.subTotal + Number(this.pagoEnvio ) - this.descuento;
            return this.costoFinal;
        }
    }
}
</script>

<style scoped>
#resumenCompraComponent {
    flex: 30%;
    background-color: #cfcdcd;
    padding: 10px;
}
.articulos {
    display: flex;
    justify-content: space-between;
}
form div label {
    margin-bottom: 20px;
}
input, select {
    width: 100%;
    height: 25px;
    font-size: 15px;
    margin-top: 15px;
    margin-bottom: 20px;
    border: 0px;
}
input {
    padding-left: 3px;
}
#evaluacion{
    margin-top: 0px;
    color: green;
}
#btn {
    background-color: rgb(230, 89, 89);
    border: 1px solid rgb(230, 89, 89);
    color:white;
    width: 100%;
    height: 30px;
    cursor: pointer;
}
</style>