<template>
    <div id="productosComponent">
        <div class="titulo">
            <h3>Carro de Compras <span>3 items</span></h3>
        </div>
        <div class="producto" v-for="(producto, index) in productos" :key="index">
            <img :src="producto.rutaImg" alt="polera azul">
            <div class="descripcion">
                <p class="nombre-producto">{{producto.titulo}}</p>
                <p>{{producto.descripcion}}</p>
            </div>
            <div class="cantidad">
                <p><a href="#" @click="disminuir(index)">-</a></p>
                <p class="num">{{ cantProduc[index] }}</p>
                <p><a href="#" @click="aumentar(index)">+</a></p>
            </div>
            <div>
                <p>${{producto.precio}}</p>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: 'productosComponent',
    props: {
        productos: {
            type: Array,
            required: true
        },
        cantProduc: {
            type: Array,
            required: true
        }
    },
    data: function() {
        return {
        }
    },
    methods: {
        disminuir: function(index) {
            this.$emit('remove', index);
        },
        aumentar: function(index) {
            this.$emit('add', index)
        }
    }
}
</script>

<style scoped>
#productosComponent {
    flex: 70%;
    padding-bottom: 10px;
}
span {
    color: rgb(136, 134, 134);
    font-size: 14px;
    margin-left: 10%;
}
.titulo {
    border-bottom: 1px solid rgb(212, 212, 212);
    height: 18%;
}
h3 {
    padding-top: 2%;
}
.producto {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 17%;
    padding: 2%;
    border-bottom: 1px solid rgb(212, 212, 212);
}
img {
    width: 15%;
}
.nombre-producto {
    color: rgb(136, 134, 134);;
}
.descripcion {
    width: 30%;
}
.cantidad {
    display: flex;
}
.num {
    border: 1px solid rgb(190, 190, 190);
    padding-left: 4px;
    padding-right: 4px;
    color: blue;
    margin-left: 5px;
    margin-right: 5px;
}
a{
    text-decoration: none;
    color: blue;
}
</style>