<template>
  <div id="app">
    <div class="container">
      <productosComponent
      v-bind:productos="datosPoleras"
      :cantProduc="cantidadProductos"
      @remove="restar"
      @add="sumar"
      />
      <resumenCompraComponent
      :numeroProductos="cantidadProductos"
      :pagoTotal="total"
      />
    </div>
  </div>
</template>

<script>
import productosComponent from '@/components/productosComponent.vue'
import resumenCompraComponent from '@/components/resumenCompraComponent.vue'

export default {
  name: 'App',
  components: {
    productosComponent,
    resumenCompraComponent
  }, 
  data: function() {
    return {
      datosPoleras: [
        {
          rutaImg: require('@/assets/Img/azul.jpg'),
          titulo: 'Polera',
          descripcion: 'Color Azúl',
          precio: 44000
        },
        {
          rutaImg: require('@/assets/Img/blanca.jpg'),
          titulo: 'Polera',
          descripcion: 'Color Blanco',
          precio: 40000
        },
        {
          rutaImg: require('@/assets/Img/roja.jpg'),
          titulo: 'Polera',
          descripcion: 'Color Rojo',
          precio: 45000
        }
      ],
      cantidadProductos: [
        0,
        0,
        0
      ],
      total: [
        0,
        0,
        0
      ]
    }
  },
  methods: {
    restar: function(index) {
      if(this.cantidadProductos[index] > 0){
          this.cantidadProductos[index]--;
          this.total[index] = this.cantidadProductos[index] * Number(this.datosPoleras[index].precio);
        }
    },
    sumar: function(index) {
      this.cantidadProductos[index]++;
      this.total[index] = this.cantidadProductos[index] * Number(this.datosPoleras[index].precio);
    }
  }
}
</script>

